/**
 * Set default table number taken from the QR-code
 * @public
 */
const setTable = function(){

    // set current table if provided
    let urlParams = new URLSearchParams(window.location.search);
    table = urlParams.get('table') ? urlParams.get('table') : "";
}

/**
 * Show small toast notification
 * @public
 */
const toast = function(msg){

    // Get the snackbar DIV
    let t = document.querySelector(".kUNwHA .snackbar");
    t.innerHTML = msg;
    t.classList.add("show");

    // After 3 seconds, remove the show class from div
    setTimeout(function(){ t.className = t.className.replace("show", ""); }, 2200);
}

/**
 * initiate Cart state
 * @public
 */
const initCart = function(){

    setBtnStep(1);
    
    try{
        
        let order = localStorage.cart;
        
        if(!order){ order = {created: Math.floor(Date.now() / 1000) }; }else{ order = JSON.parse(order); }
        if(typeof(order.items) === 'undefined'){ order.items = {}; }

        // check if cart expired 1h session
        if(order.created+3600 < Math.floor(Date.now() / 1000)) {

            order.items = {};
            order.created = Math.floor(Date.now() / 1000);
        }

        cart.state.order = order;
        // console.log(order);
    }catch(e){
        
        cart.state.order.created = Math.floor(Date.now() / 1000);
        cart.state.order.items = {};
    }

    // make sure user ID exists
    if(!localStorage.idd) localStorage.idd = randomString(8)+Math.floor(Date.now());

    // console.log(cart.state.order);
    cart.refreshCheckoutButton();
}

/**
 * Init call to action button listeners
 * @public
 */
const btnListeners = function(){

    // continue to table selection
    document.querySelector(".kUNwHA .cta-btn .mbtn").addEventListener('click', function(e) {

        document.querySelector("body").classList.add('kp-modal');
        refreshDialogView();

        // view cart
        if(cart.state.order.step == 1){

            viewCheckoutModal();
            setBtnStep(2);
            return;
        }
        
        // pick table number view
        if(cart.state.order.step == 2){
            
            // view contents
            let html = '';
            html = '<div class="ptable">';
            html +=	'<label for="table" style="'+(table.length>0?'display:none;':'')+'">Table number</label>';
            html +=	'<input type="number" value="'+table+'" name="table" style="'+(table.length>0?'display:none;':'')+'" autocomplete="off" class="table" size="4" pattern="" inputmode="">';
            html +=	'<label for="note">Note</label>';
            html += '<textarea class="note" name="note" placeholder="leave a note for a kitchen"></textarea>';
            html +=	'</div>';

            document.querySelector(".kUNwHA .cdialog-cnt .kp-body").innerHTML = html;
            document.querySelector(".kUNwHA .cdialog-cnt .ptable .table").focus();

            setBtnStep(3);
            return;
        }

        // finalize checkout
        if(cart.state.order.step == 3){

            // validate input field
            let table = document.querySelector(".kUNwHA .cdialog-cnt .ptable .table").value;
            let note = document.querySelector(".kUNwHA .cdialog-cnt .ptable .note").value;
            if(table==""){ alert("Please enter table number"); return; }

            // get note
            cart.state.order.table = table;
            cart.state.order.note = note;

            // cache data
            localStorage.cart = JSON.stringify(cart.state.order);

            // close checkout modal
            let mdialogCnt = document.querySelector(".kUNwHA .cdialog-cnt");
            mdialogCnt.style.display = "none";

            // hide button
            document.querySelector(".kUNwHA .cta-btn").style.display = "none";

            // restore body scrolling params
            closeModal();
            
            // send order to backend
            authUser();
            
            document.querySelector(".kUNwHA .overlay").style.display = "block";
            document.querySelector(".kUNwHA .overlay .loader").style.display = "block";

            return;
        }

        // offer sign in
        if(cart.state.order.step == 4){

            // restore body scrolling params
            closeModal();

            // window.location.href = 'https://auth.kenzap.com/?redirect=https%3A%2F%2Faccount.kenzap.com%2F%3Flauncher%3Dmenu-orders&idd=' + localStorage.idd;
            return;
        }
    });
}

/**
 * set button Call to action step
 * @public
 */
const setBtnStep = function(step){

    document.querySelector(".kUNwHA .cta-btn").style.display = "block";
    switch(step){

        case 1: cart.state.order.step = step; document.querySelector(".kUNwHA .cta-btn .mbtn").innerHTML = 'View basket <span class="price">s$0.00</span>'; break;
        case 2: cart.state.order.step = step; document.querySelector(".kUNwHA .cta-btn .mbtn").innerHTML = 'Continue'; break;
        case 3: cart.state.order.step = step; document.querySelector(".kUNwHA .cta-btn .mbtn").innerHTML = 'Checkout';  break;
        case 4: cart.state.order.step = step; document.querySelector(".kUNwHA .cta-btn .mbtn").innerHTML = "Back to menu"; break;
    }
}

/**
 * Gets menu data
 * @public
 */
const getMenu = function(){

    const limit = 1000;

    // let page = getPage(); 
    let offset = 0;
    let sid = spaceID;
    
    fetch('https://api-v1.kenzap.cloud/', {
        method: 'post',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'text/plain',
            'Authorization': 'Bearer ' + API_KEY,
        },
        body: JSON.stringify({
            query: {
                /* user: {
                    type:       'authenticate',
                    fields:     ['avatar'],
                    token:      getCookie('kenzap_token')
                },
                locale: {
                    type:       'locale',
                    id:         getCookie('lang')
                },
                */
                items: {
                    type:       'find',
                    key:        'ecommerce-product',
                    // sid:        sid,
                    fields:     ['_id', 'id', 'img', 'status', 'price', 'priced', 'title', 'cats', 'sdesc', 'ldesc', 'variations', 'updated'],
                    limit:      limit,
                    offset:     offset,
                    sortby:     {
                                    field: 'created',
                                    order: 'ASC'
                                }
                },
                settings: {
                    type:       'get',
                    key:        'qrmenu-settings',
                    fields:     ['mode', 'palette', 'categories'],
                }
            }
        })
    })
    .then(response => response.json())
    .then(response => {

        if (response.success){

            products = response;

            spaceID = response.sid;

            renderMenu();

            dialogListeners();

            menuListeners();

            sliderListeners();

            btnListeners();
            
            console.log(response);
        
        }else{

            // parseApiError(response);
        }
    })
    .catch(error => {
        console.error('Error:', error);
    });
}

/**
 * Render menu dialog
 * @public
 */
const renderMenu = function(){

    let rows = products.settings.categories.split('\n');
    let html_slider = '';
    let html_menu = '';
    let title = '';

    for(let c of rows){

        
        // get values
        let val = c.split('|');
        let cat = val[0].trim();
        let note = val[1] == null ? '' : val[1].trim();
        console.log(note);

        // render slider
        html_slider += '<div class="slide" data-href="'+convertToSlug(cat)+'"><a href="#'+convertToSlug(cat)+'" class=" cl">'+cat+'</a></div>'; 

        // render menu
        for(let x in products['items']){

            // centralize IDS
            products['items'][x].id = products['items'][x]._id;

            // skip current loop if current category not present
            if(typeof(products['items'][x]['cats']) === 'undefined') continue;
            if(products['items'][x]['cats'] == null) continue;
            if(!products['items'][x]['cats'].includes(cat)) continue;

            // product image
            let img = 'https://cdn.kenzap.com/loading.png';

            // load image asyncronously
            if(products['items'][x]['img'][0] || products['items'][x]['img'][0] == 'true'){

                let imgl = new Image();
                imgl._id = products['items'][x]['_id'];
                imgl.onload = function () { console.log('adding'+this.src); document.querySelector(".kUNwHA .kenzap-row[data-id='"+this._id+"'] img").setAttribute('src', this.src); }
                imgl.src = CDN + '/S'+spaceID+'/product-'+products['items'][x]['_id']+'-1-250.jpeg?'+products['items'][x]['updated'];
            }

            // price
            let price = '<span class="tag ptag">'+priceFormat(products['items'][x]['price'])+'</span>';
            if(products['items'][x]['priced']!=''){

                price = '<span class="ptagc">'+priceFormat(products['items'][x]['price'])+'</span> <span class="tag ptag">'+priceFormat(products['items'][x]['priced'])+'</span>';
            }

            let ctag = typeof(cart.state.order.items[products['items'][x]['_id']]) === 'undefined' ? "" : cart.state.order.items[products['items'][x]['_id']].qty;
            let show_heading = '';
            if(title != cat){ title = cat; show_heading = '<h2 id="'+convertToSlug(title)+'">'+title+'</h2><p class="subnote">'+note+'</p>'; }
            html_menu += '\
            <div class="kenzap-row" data-index="'+x+'" data-id="'+products['items'][x]['_id']+'">\
                '+show_heading+'\
                <div class="info-box">\
                    <div class="kenzap-col-7">\
                        <div class="kp-content">\
                            <h3><span class="tag ctag">'+ctag+'</span>'+products['items'][x]['title']+'</h3>\
                            <p>'+products['items'][x]['sdesc']+'</p>\
                            '+price+'\
                        </div>\
                    </div>\
                    <div class="kenzap-col-5">\
                        <div class="kp-img">\
                            <img src="'+img+'" alt="'+products['items'][x]['title']+'">\
                        </div>\
                    </div>\
                </div>\
                \
            </div>';
        }
    }

    let slider_container = document.querySelector(".kUNwHA .slideset");
    let menu_container = document.querySelector(".kUNwHA .kenzap-container");
    if(html_menu==''){
        menu_container.innerHTML = "Please add products and specify categories first";
    }else{
        slider_container.innerHTML = html_slider;
        menu_container.innerHTML = html_menu;
    }	
}

/**
 * Dialog box listeners
 * @public
 */
const dialogListeners = function(){

    let close = document.querySelector(".kUNwHA .mdialog .close");
    close.addEventListener('click', function(e) {

        closeModal();
    });

    let close2 = document.querySelector(".kUNwHA .cdialog .close");
    close2.addEventListener('click', function(e) {

        let cdialogCnt = document.querySelector(".kUNwHA .cdialog-cnt");
        cdialogCnt.style.display = "none";

        setBtnStep(1);

        closeModal();
    });

    window.addEventListener("resize", refreshDialogView);
    window.addEventListener("orientationchange", refreshDialogView);
}

/**
 * Close product popup
 * @public
 */
const closeModal = function(){

    let mdialogCnt = document.querySelector(".kUNwHA .mdialog-cnt");
    mdialogCnt.style.display = "none";

    document.querySelector(".kUNwHA .overlay").style.display = "none";

    // allow body scroll
    document.querySelector(".kUNwHA .scrollable").style.height = 'auto';
    document.querySelector(".kUNwHA .scrollable").style.overflowY = 'scroll';

    document.querySelector("body").style.overflowY = 'auto';
    document.querySelector("body").style.height = 'auto';
    setTimeout(function(){ document.querySelector("body").classList.remove('kp-modal'); },300);

    document.documentElement.scrollTop = scroll.last_state;

    let cdialogCnt = document.querySelector(".kUNwHA .cdialog-cnt");
    cdialogCnt.style.display = "none";

    cart.refreshCheckoutButton();
}

/**
 * Dialog box listeners
 * @public
 */
const refreshDialogView = function(){

    // body prevent scroll
    if(document.querySelector("body").classList.contains('kp-modal')){
        document.querySelector("body").style.height = '100vh';
        document.querySelector("body").style.overflowY = 'hidden';
        document.querySelector(".kUNwHA .scrollable").style.height = '100vh';
        document.querySelector(".kUNwHA .scrollable").style.overflowY = 'hidden';
        document.querySelector(".kUNwHA .mdialog-cnt").style.height = (window.screen.availHeight-160) + "px";
    }
    // document.querySelector(".kUNwHA .cdialog-cnt").style.height = (window.screen.availHeight-160) + "px";
}

/**
    * Menu horizontal slider listeners listener.
    * @public
    */
const menuListeners = function(){

    // menu row click listener
    let row_list = document.querySelectorAll(".kUNwHA .kenzap-row");
    for(const row of row_list){

        row.addEventListener('click', function(e) {

            let x = this.dataset.index; cart.state.index = x;
            let product_id = products['items'][x].id;
            let mdialogCnt = document.querySelector(".kUNwHA .mdialog-cnt");
            scroll.last_state = scroll.state;

            // console.log(cart.state.order.items);

            cart.state.total = 0;
            cart.state.count = 0;

            // update cart state
            cart.state.product = typeof(cart.state.order.items[product_id]) === 'undefined' ? {id: products['items'][x].id, title: products['items'][x].title, sdesc: products['items'][x].sdesc, index: x, qty: 0, note: "", variations:[] }:cart.state.order.items[product_id];
            
            // detect product state
            cart.state.product.type = cart.state.product.qty == 0 ? "new" : "update";
            
            document.querySelector("body").classList.add('kp-modal');
            // console.log(cart.state.product.variations);

            refreshDialogView();

            let src = (products['items'][x]['img'][0]) ? CDN + '/S'+spaceID+'/product-'+products['items'][x]['_id']+'-1-250.jpeg?'+products['items'][x]['updated'] : 'https://cdn.kenzap.com/loading.png'


            // bind data to modal body
            document.querySelector(".kUNwHA .mdialog-cnt .mdialog .kp-body > h2").innerHTML = cart.state.product.title;
            document.querySelector(".kUNwHA .mdialog-cnt .mdialog .kp-body > p").innerHTML = cart.state.product.sdesc;
            document.querySelector(".kUNwHA .mdialog-cnt .mdialog .kp-img img").setAttribute('src', src);
            document.querySelector(".kUNwHA .mdialog .qty").value = cart.state.count = cart.state.product.qty;	
            document.querySelector(".kUNwHA .mdialog .kp-note textarea").value = cart.state.product.note;	 

            // load image asyncronously
            var imgl = new Image();
            imgl.onload = function () { document.querySelector(".kUNwHA .mdialog-cnt .mdialog .kp-img img").setAttribute('src', this.src); }
            imgl.src = CDN + '/S'+spaceID+'/product-'+products['items'][x]['_id']+'-1-500.jpeg?'+products['items'][x]['updated'];

            // "variations":[{"title":"Sauce","type":"checkbox","required":"0","data":[{"title":"Chilli","price":"0.40","cond":""},{"title":"Teriyaki","price":"1.00","cond":""}]},{"title":"Rice","type":"checkbox","required":"1","data":[{"title":"rice","price":"0.00","cond":""},{"title":"cdscdsc","price":"0.00","cond":""}]}]}
            let html_vars = '';
            if(typeof(products['items'][x].variations !== 'undefined'))
            for(let v in products['items'][x].variations){

                // products['items'][x].variations[v].title

                // variation type
                let type = '';	
                if(products['items'][x].variations[v].type=='checkbox') type = 'check';
                if(products['items'][x].variations[v].type=='radio') 	type = 'radio';

                // update cart state
                if(typeof(cart.state.product.variations[v]) === 'undefined') cart.state.product.variations[v] = {title: products['items'][x].variations[v].title, required: products['items'][x].variations[v].required, allow: products['items'][x].variations[v].required == '1'?false:true };	

                // struct variation
                html_vars += '\
                <h3>'+products['items'][x].variations[v].title+(products['items'][x].variations[v].required == '1' ? ' <span class="tag">required</span>':'')+'</h3>\
                <div class="kp-'+type+'">';

                // variation labels
                for(let d in products['items'][x].variations[v].data){

                    let checked = false;
                    // console.log(cart.state.product.variations);
                    if(typeof(cart.state.product.variations[v]) !== 'undefined' && typeof(cart.state.product.variations[v].list) !== 'undefined' && typeof(cart.state.product.variations[v].list["_"+d]) !== 'undefined'){

                        checked = true;
                        // alert("checked");
                        // checked = typeof(cart.state.product.variations[v].list["_"+d]) === 'undefined' ? false : true;
                    }
                    
                    switch(type){
                        case 'check':

                        html_vars += '\
                            <label>\
                                <input type="checkbox" data-required="'+products['items'][x].variations[v].required+'" data-indexv="'+v+'" data-index="'+d+'" data-title="'+products['items'][x].variations[v].data[d]['title']+'" data-price="'+products['items'][x].variations[v].data[d]['price']+'" '+(checked?'checked="checked"':'')+'>\
                                <div class="checkbox">\
                                    <svg width="20px" height="20px" viewBox="0 0 20 20">\
                                        <path d="M3,1 L17,1 L17,1 C18.1045695,1 19,1.8954305 19,3 L19,17 L19,17 C19,18.1045695 18.1045695,19 17,19 L3,19 L3,19 C1.8954305,19 1,18.1045695 1,17 L1,3 L1,3 C1,1.8954305 1.8954305,1 3,1 Z"></path>\
                                        <polyline points="4 11 8 15 16 6"></polyline>\
                                    </svg>\
                                </div>\
                                <span>'+products['items'][x].variations[v].data[d]['title']+'</span>\
                                <div class="price">+ '+priceFormat(products['items'][x].variations[v].data[d]['price'])+'</div>\
                            </label>';
                        
                        break;
                        case 'radio':

                        html_vars += '\
                            <label>\
                                <input type="radio" data-required="'+products['items'][x].variations[v].required+'" data-indexv="'+v+'" name="radio" data-index="'+d+'" data-title="'+products['items'][x].variations[v].data[d]['title']+'" data-price="'+products['items'][x].variations[v].data[d]['price']+'" '+(checked?'checked="checked"':'')+' />\
                                <span>'+products['items'][x].variations[v].data[d]['title']+'</span>\
                                <div class="price">+ '+priceFormat(products['items'][x].variations[v].data[d]['price'])+'</div>\
                            </label>';
                        
                        break;
                    }
                }

                html_vars += '</div>';

                // html_var = '\
                // <h3>'+products['items'][x].variations[v].title+' '+(products['items'][x].variations[v].required == '1' ? '<span class="tag">required</span>':'')+'</h3>\
                // <div class="kp-'+type+'">\
                // 	'+labels+'\
                // </div>';
            }

            document.querySelector(".kUNwHA .mdialog-cnt .mdialog .kp-vars").innerHTML = html_vars;

            cart.refreshButton();
            
            mdialogCnt.style.display = "block";
            document.querySelector(".kUNwHA .overlay").style.display = "block";
    
            // checkbox listener
            let checkbox_list = document.querySelectorAll(".kUNwHA .mdialog .kp-check input[type=checkbox]");
            for(const cb of checkbox_list){
                cb.addEventListener('change', function() {

                    console.log("call refreshButton");

                    let v = parseInt(cb.dataset.indexv);
                    // if(typeof(cart.state.product.variations[v].list) === 'undefined') 
                    cart.state.product.variations[v].list = {};

                    let count = 0;
                    for(const cbg of document.querySelectorAll(".kUNwHA .mdialog .kp-check input[type=checkbox][data-indexv='"+v+"']")){

                        // checks if this block is required and allows checkout
                        if(cb.dataset.required=="1"){ if(count) cart.state.product.variations[v].allow = true; }else{ cart.state.product.variations[v].allow = true; }

                        // cache selected variations
                        if(cbg.checked){ cart.state.product.variations[v].list["_"+cbg.dataset.index] = {title: cbg.dataset.title, price: parseFloat(cbg.dataset.price) }; count +=1; }
                    }
                    
                    // console.log(cart.state.product.variations);
                    cart.refreshButton();
                });
            }

            // radio listener
            let radio_list = document.querySelectorAll(".kUNwHA .mdialog .kp-radio input[type=radio]");
            for(const cb of radio_list){
                cb.addEventListener('change', function() {

                    console.log("call refreshButton");

                    let v = parseInt(cb.dataset.indexv);
                    // if(typeof(cart.state.product.variations[v].list) === 'undefined') 
                    cart.state.product.variations[v].list = {};

                    let count = 0;
                    for(const rag of document.querySelectorAll(".kUNwHA .mdialog .kp-radio input[type=radio][data-indexv='"+v+"']")){
                    
                        // cache selected variations
                        if(rag.checked){ cart.state.product.variations[v].list["_"+rag.dataset.index] = {title: rag.dataset.title, price: parseFloat(rag.dataset.price) }; count +=1; }
                        
                        // checks if this block is required and allows checkout
                        if(cb.dataset.required=="1"){ if(count) cart.state.product.variations[v].allow = true; }else{ cart.state.product.variations[v].allow = true; }
                    }

                    // console.log(cart.state.product.variations);

                    cart.refreshButton();
                });
            }
        });
        // cart.refreshButton();
        // console.log(cart.state.product);
    }

    // plus click listener
    let qty = document.querySelector(".kUNwHA .mdialog .qty");
    let plus = document.querySelector(".kUNwHA .mdialog .plus");
    plus.addEventListener('click', function(e) {

        if(cart.state.count < config.cart.max_addition) cart.state.count +=1;
        qty.value = cart.state.count;
        cart.refreshButton();
        vibrate();
    });
    
    // minus click listener
    let minus = document.querySelector(".kUNwHA .mdialog .minus");
    minus.addEventListener('click', function(e) {

        if(cart.state.count > 0) cart.state.count -=1;
        qty.value = cart.state.count;
        cart.refreshButton();
        vibrate();
    });

    // add to cart listener
    let add = document.querySelector(".kUNwHA .mdialog .add");
    add.addEventListener('click', function(e) {

        if(this.parentNode.classList.contains("dis")){ alert("Please select required fields"); return; }
        cart.addToCart();
        // cart.resetButton(); 
        closeModal();
        toast("Cart updated");
        vibrate();
    });

    // checkout  button listener
    // let checkout = document.querySelector(".kUNwHA .vadd");
    // checkout.addEventListener('click', function(e) {

    // 	viewCheckoutModal();
    // });
}

/**
    * view checkout modal
    * @public
    */
const viewCheckoutModal = function(){

    let cdialogCnt = document.querySelector(".kUNwHA .cdialog-cnt");

    refreshDialogView();

    cdialogCnt.style.display = "block";
    document.querySelector(".kUNwHA .overlay").style.display = "block";

    // generate cart table
    let html = '<table>';
    let total = 0;
    for(let p in cart.state.order.items){

        // console.log(cart.state.order.items[p].priceF);
        total += parseFloat(cart.state.order.items[p].priceF);
        html += '<tr><td data-id="'+cart.state.order.items[p].id+'" class="checkt">'+cart.state.order.items[p].qty+' x '+cart.state.order.items[p].title+'</td><td></td><td class="price"><strong>'+priceFormat(cart.state.order.items[p].price)+'</strong></td></tr>';
    }

    html += '<tr><td class="summ" colspan="3">Total <strong>'+priceFormat(total)+'</strong></td></tr>';
    html += '</table>';

    cdialogCnt.querySelector(".kUNwHA .cdialog-cnt .kp-body").innerHTML = html;

    // remove item from cart
    for(const td of cdialogCnt.querySelectorAll(".kUNwHA .cdialog-cnt .kp-body .checkt")){

        td.addEventListener('click', function(e) {

            let c = confirm("Remove "+this.innerHTML + "?");
            if(c){

                document.querySelector(".kUNwHA .kenzap-row[data-id='"+this.dataset.id+"'] .ctag").innerHTML = "";
                cart.removeFromCart(this.dataset.id);
                cart.refreshCheckoutButton();

                // console.log(cart.state.order.items);
                if(Object.keys(cart.state.order.items).length == 0){

                    setBtnStep(1);
                    // console.log("closeModal"); 
                    closeModal(); 
                    return; 
                }
                viewCheckoutModal();
                setBtnStep(2);
            }
        });
    }	
}

/**
    * view thank you modal
    * @public
    */
