/**
    * Menu horizontal slider listeners.
    * @public
    */
const sliderListeners = function(){

    let slide_list = document.querySelectorAll(".kUNwHA .slideset .slide");
    for(const slide of slide_list){

        // offsetLeft
        let scrollDiv = slide.offsetLeft;
        scroll.offsets[slide.dataset.href] = scrollDiv;
        slide.addEventListener('click', function(e) {

            let sto = document.documentElement.scrollTop;
            // document.documentElement.scrollTop = sto-=10;
            setTimeout(function(el){

                for (const l of document.querySelectorAll(".kUNwHA .slide")){ l.classList.remove("active"); }
                el.classList.add("active");
                // document.documentElement.scrollTop = sto+=10;
            },250, this)
        });
    }
}

/**
    * Scroll listener. Add/Remove scrolled class to body
    * @public
    */
    const scrollEvents = function() {

    // console.log(document.documentElement.scrollTop);
    let h_list = document.querySelectorAll(".kUNwHA .kenzap-row h2");
    let s_id = "";
    let diff_prev = 0;
    let diff = 0;
    scroll.state = document.documentElement.scrollTop;

    for (const h of h_list){

        diff = (h.offsetTop - document.documentElement.scrollTop);
        
        // scrolling up
        // if(scroll.state<scroll.state_prev){

        // 	scroll.direction = "up";
        // 	if(diff>0){
                
        // 		if(scroll.el_id == "") scroll.el_id = h.id;
        // 		if(diff_prev > diff){ console.log(diff + " " +h.id); diff = diff_prev; scroll.el_id = h.id; }
        // 	}
        // }

        // scrolling down
        // if(scroll.state>scroll.state_prev){

            scroll.direction = "down";
            if(diff<0){

                if(diff_prev > diff){ diff = diff_prev; scroll.el_id = h.id; }
            }
    
            // if(diff>0){

            // }
        // }

        diff_prev = diff;
    }

    // save previous scroll state
    if(scroll.state_prev!=scroll.state) scroll.state_prev = scroll.state;

    if(scroll.el_id!=""){

        let sc = document.querySelector(".kUNwHA .cata-sub-nav");
        if(scroll.timer) clearTimeout(scroll.timer);
        scroll.timer = setTimeout((el_id) => {

            sc.scrollTo({ left: scroll.offsets[el_id], behavior: 'smooth'});

            for (const l of document.querySelectorAll(".kUNwHA .slide")){ l.classList.remove("active"); }
            document.querySelector(".kUNwHA [data-href='"+el_id+"']").classList.add("active");

        }, 200, scroll.el_id);
    }

    scroll.el_id = "";
}

/**
    * Convert text to url friendly slug 
    * @public
    */
const convertToSlug = function(str) {

    return str
        .toLowerCase()
        .replace(/[^\w ]+/g,'')
        .replace(/ +/g,'-')
        ;
}

/**
    * Render price
    * @public
    */
const priceFormat = function(price) {

    let priceF = parseFloat(price).toFixed(2);

    switch(config.price.style){
        case 'left': priceF = config.price.symbol + priceF; break;
        case 'right': priceF = priceF + config.price.symbol; break;
    }

    return priceF;
}

/**
    * Generates a random string
    * 
    * @param int length_
    * @return string
    */
const randomString = function(length_) {

    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghiklmnopqrstuvwxyz'.split('');
    if (typeof length_ !== "number") {
        length_ = Math.floor(Math.random() * chars.length_);
    }
    var str = '';
    for (var i = 0; i < length_; i++) {
        str += chars[Math.floor(Math.random() * chars.length)];
    }
    return str;
}

