/**
    * Cart and checkout functions
    * @public
    */
let cart = {

    state: {total: 0, count: 0, index: 0, product: { variations: [] }, order: {} },
    // order:{total: 0, items: []},
    resetButton: function(){

        // document.querySelector(".kUNwHA .qty").value = 0;
        this.state.total = 0;
        this.state.count = 0;
        // this.state.product = { variations: [] };
        // this.refreshButton();
    },
    refreshButton: function(){

        // price | get base product price
        this.state.product.qty = parseInt(document.querySelector(".kUNwHA .qty").value);
        this.state.product.price = products['items'][this.state.index].priced == "" ? parseFloat(products['items'][this.state.index].price) : parseFloat(products['items'][this.state.index].priced);

        // get note
        this.state.product.note = document.querySelector(".kUNwHA .kp-note textarea").value;

        // price | calculate upon checkbox variations
        let cb_count = 0;
        let checkbox_list = document.querySelectorAll(".kUNwHA .mdialog .kp-check input[type=checkbox]");
        for(const cb of checkbox_list){ if(cb.checked){ cb_count++; this.state.product.price+=parseFloat(cb.dataset.price);  } } //console.log("CB: " + parseFloat(cb.dataset.price));

        // price | calculate upon radio variations
        let ra_count = 0;
        let radio_list = document.querySelectorAll(".kUNwHA .mdialog .kp-radio input[type=radio]");
        for(const ra of radio_list){ if(ra.checked){ ra_count++; this.state.product.price+=parseFloat(ra.dataset.price); } } //console.log("RA: " + parseFloat(ra.dataset.price)); 

        // price | assign to button
        this.state.product.priceF = parseFloat((this.state.product.price*this.state.product.qty));
        document.querySelector(".kUNwHA .add .price").innerHTML = priceFormat(this.state.product.priceF);

        // define button state, enabled, disabled
        let dis = false;
        if(this.state.product.priceF==0) dis = true;
        for(v in cart.state.product.variations){ if(cart.state.product.variations[v].allow == false) dis = true; }

        // define button text
        // console.log(cart.state.product.type);
        document.querySelector(".kUNwHA .kp-add .mbtn .add").style.background = "";
        if(cart.state.product.type == 'update' && this.state.product.qty > 0){ document.querySelector(".kUNwHA .kp-add .mbtn .cta").innerHTML = "Update"; }
        if(cart.state.product.type == 'update' && this.state.product.qty == 0){ document.querySelector(".kUNwHA .kp-add .mbtn .cta").innerHTML = "Remove"; document.querySelector(".kUNwHA .kp-add .btn .add").style.background="#df1960"; dis = false; }
        if(cart.state.product.type == 'new'){ document.querySelector(".kUNwHA .kp-add .mbtn .cta").innerHTML = "Add"; }

        // refresh UI
        if(dis){ document.querySelector(".kUNwHA .kp-add .mbtn").classList.add("dis"); }else{ document.querySelector(".kUNwHA .kp-add .mbtn").classList.remove("dis"); }
    },
    refreshCheckoutButton: function(){

        // console.log(this.state.order.items);
        let total = 0;
        for(let p in this.state.order.items){

            // console.log(this.state.order.items[p].priceF);
            total += parseFloat(this.state.order.items[p].priceF);
        }

        this.state.order.total = total;

        // trigger visibility
        if(total>0) {

            document.querySelector("body").classList.add("cbtn");
            
            setBtnStep(1);

            document.querySelector(".kUNwHA .cta-btn .price").innerHTML = priceFormat(total);
        }else{
            document.querySelector(".kUNwHA .cta-btn").style.display = "none";
            document.querySelector("body").classList.remove("cbtn");
        }
    },
    addToCart: function(){

        // sync UI with data
        // this.refreshButton();

        // add to cart
        this.state.order.items[this.state.product.id] = this.state.product; // push(this.state.product);

        console.log(this.state.order.items);

        // remove from cart
        if(this.state.product.qty==0){ this.removeFromCart(this.state.product.id); }else{ document.querySelector(".kUNwHA .kenzap-row[data-id='"+this.state.product.id+"'] .ctag").innerHTML = this.state.product.qty; }

        // cache results
        localStorage.cart = JSON.stringify(this.state.order);

        // setBtnStep(1);
        cart.refreshCheckoutButton();
        
    },
    removeFromCart: function(id){

        delete this.state.order.items[id];

        document.querySelector(".kUNwHA .kenzap-row[data-id='"+id+"'] .ctag").innerHTML = "";
 
        // cache results
        localStorage.cart = JSON.stringify(this.state.order);
    },
    clearCart: function(){

        // clear cart and refresh UI
        for(i in cart.state.order.items){  document.querySelector(".kUNwHA .kenzap-row[data-id='"+cart.state.order.items[i].id+"'] .ctag").innerHTML = ""; }

        // reser cached dara
        cart.state.order = {};
        cart.state.order.created = Math.floor(Date.now() / 1000);
        cart.state.order.items = {};

        localStorage.cart = JSON.stringify(cart.state.order);

        // refresh cart button 
        this.refreshCheckoutButton();
    }
}